.cls-1,.cls-4{fill:#fff;}.cls-2{fill:#532c7b;}.cls-3{fill:#350d65;}.cls-4{opacity:0.1;}
body{overflow-x:hidden}h1{font-size:54px}h2{font-size:40px}.landing-page-component-wrapper{background-color:black;width:100%;height:100%;position:static;color:white;top:0;left:0;min-height:100vh;min-width:100vw}.landing-page-component-wrapper .landing-page{color:white}.landing-page-component-wrapper .landing-page-page-title-section{display:flex;flex-direction:column;justify-content:center;align-items:center;min-width:100vw;min-height:100vh}.landing-page-component-wrapper .landing-page-page-title-section h1{z-index:1}.landing-page-component-wrapper .content-section:nth-child(even){flex-direction:row-reverse}.landing-page-component-wrapper .content-section-images:nth-child(odd){flex-direction:row-reverse}.landing-page-component-wrapper .content-section-images:nth-child(even){flex-direction:row}.newsletter{background-color:#c50f0f;color:#fff;padding:80px 0;display:flex;flex-direction:column;justify-content:center;align-items:center}.newsletter_input-section{display:flex;flex-direction:row}.newsletter_input-section--icon{width:20px;margin-top:-10px;margin-left:-20px}.input-newsletter{background:transparent;border:none;border-bottom:1px solid white;min-width:400px;color:white}.input-newsletter:focus{outline:none}::-webkit-input-placeholder{color:white;opacity:1}:-ms-input-placeholder{color:white;opacity:1}::-ms-input-placeholder{color:white;opacity:1}::placeholder{color:white;opacity:1}.bg-animation{position:absolute;height:100vh;width:200vw;z-index:0;background:linear-gradient(100deg, #000 40%, #212121 63%, #000 79%)}@media (min-width: 0px){.content-section{display:flex;flex-wrap:wrap;height:100vh;overflow:hidden;flex:1 1 768px}.content-section-text-section{flex:1 1 500px;display:flex;flex-direction:column;justify-content:center;color:white;align-items:center;text-align:center}.content-section-text-section-text{max-width:500px}.content-section-images{flex:1 1 500px;display:flex;justify-content:center}.content-section-images img:nth-child(1){z-index:1;height:480px;margin-top:120vh}.content-section-images img:nth-child(2){z-index:0;margin-left:-50px;margin-top:-50px;height:530px;margin-top:140vh;display:none}h1{font-size:34px !important}.footer-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:50px;padding-bottom:50px}.footer-wrapper .social-media-icons{display:flex;justify-content:center;align-items:center}.footer-wrapper .social-media-icons img{width:60px;padding:10px}.footer-wrapper .footer-links{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:30px}.footer-wrapper .footer-links .text-item{padding-left:10px;padding-right:10px;padding-bottom:10px}}@media (min-width: 1008px){h1{font-size:54px !important}.footer-wrapper .footer-links{flex-direction:row}}@media (min-width: 1208px){.content-section img:nth-child(2){display:block}}

